<script>
  export let header = { titreCycle: "" };
  export let data = [];

  import { concatDates } from "../lib/format.js";
  import dayjs from "dayjs";
  dayjs.locale("fr");
  import "../lib/dayjs_custom_locale_fr.js";
  import _ from "lodash";

  // La première et dernière date de séances du cycle
  $: minMaxDates = _(data)
    .map(d =>
      _(d.items)
        .map(e =>
          _(e.seance)
            .map(f => f.dateHeure)
            .value()
        )
        .value()
    )
    .flattenDeep()
    .thru(d => [_.min(d), _.max(d)])
    .value();

  function stringOnly(d) {
    return typeof d === "string" ? d : "";
  }
</script>

<style type="text/postcss">
  section {
    display: -webkit-box;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
            flex-flow: row wrap;
    max-width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .col1 {
    -webkit-box-flex: 0;
            flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 7/12 - (30px - 30px * 7/12));
    max-width: calc(99.9% * 7/12 - (30px - 30px * 7/12));
    width: calc(99.9% * 7/12 - (30px - 30px * 7/12));
    min-height: 300px;
  }

  .col1:nth-child(1n) {
    margin-right: 30px;
    margin-left: 0;
  }

  .col1:last-child {
    margin-right: 0;
  }

  .col1:nth-child(12n) {
    margin-right: 0;
    margin-left: auto;
  }

  .col2 {
    -webkit-box-flex: 0;
            flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(99.9% * 5/12 - (30px - 30px * 5/12));
    max-width: calc(99.9% * 5/12 - (30px - 30px * 5/12));
    width: calc(99.9% * 5/12 - (30px - 30px * 5/12));
    /* min-height: 300px; */
  }

  .col2:nth-child(1n) {
    /* min-height: 300px; */
    margin-right: 30px;
    margin-left: 0;
  }

  .col2:last-child {
    /* min-height: 300px; */
    margin-right: 0;
  }

  .col2:nth-child(12n) {
    /* min-height: 300px; */
    margin-right: 0;
    margin-left: auto;
  }

  /* h1 {
    margin: 6px 0 12px 0;
  } */

  a.surtitre {
    display: inline-block;
    background-color: #ccc;
    color: #000;
    padding: 4px 12px;
    margin-bottom: 12px;
    font-family: Nunito Sans;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 1rem;
    letter-spacing: -0.05rem;
  }

  .datesCycle {
    font-family: Nunito Sans;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 1.25rem;
    letter-spacing: -0.05rem;
    margin-top: 12px;
    margin-bottom: 24px;
  }

/*# sourceMappingURL=src.map */</style>

<div>
  {#if header.titreSurcycle}
    <a class="surtitre" href="javascript:void 0;">{header.titreSurcycle}</a>
  {/if}
</div>
<h1>{header.titreCycle}</h1>
<div class="datesCycle">
  {concatDates(dayjs(minMaxDates[0]).format(`D MMMM YYYY`), dayjs(minMaxDates[1]).format(`D MMMM YYYY`), ' ', 'Du ', ' au ')}
</div>
<section>
  <div class="col1">
    {@html stringOnly(header.texte)}
  </div>
  <div class="col2" />
</section>
